document.addEventListener('DOMContentLoaded', () => {
  const faqTrack = document.querySelector('[data-faq-track]');
  const faqItems = faqTrack.querySelectorAll('[data-faq-item]');
  const prevBtns = document.querySelectorAll('[data-faq-nav-prev]');
  const nextBtns = document.querySelectorAll('[data-faq-nav-next]');
  let currentIndex = 0;

  function updateFAQ(toggle = false, toggleIndex = null) {
    faqItems.forEach((item, index) => {
      item.classList.remove('faq-focused');

      if (toggle && index === toggleIndex) {
        // –ü–µ—Ä–µ–∫–ª—é—á–∏—Ç–∏ –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ –µ–ª–µ–º–µ–Ω—Ç–∞
        if (item.hasAttribute('data-open')) {
          item.removeAttribute('data-open');
        } else {
          // –ó–∞–∫—Ä–∏–≤–∞—î–º–æ —ñ–Ω—à–∏—Ö, –≤—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ —Ü–µ–π
          faqItems.forEach(i => i.removeAttribute('data-open'));
          item.setAttribute('data-open', 'true');
          currentIndex = index;
        }
      }
    });

    // –Ø–∫—â–æ toggle –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω–æ –∞–±–æ –ø—ñ—Å–ª—è toggle –ø–æ—Ç—Ä—ñ–±–Ω–æ –ø—ñ–¥—Å–≤—ñ—Ç–∏—Ç–∏ —ñ —Å–∫—Ä–æ–ª–∏—Ç–∏ –ø–æ—Ç–æ—á–Ω–∏–π
   if (!toggle) {
  faqItems.forEach((item, index) => {
    if (index === currentIndex) {
      item.setAttribute('data-open', 'true');
    } else {
      item.removeAttribute('data-open');
    }

    // üëá –ü—ñ–¥—Å–≤—ñ—á—É—î–º–æ —Ç—ñ–ª—å–∫–∏ –ù–ï–≤—ñ–¥–∫—Ä–∏—Ç—É –∞–∫—Ç–∏–≤–Ω—É –∫–∞—Ä—Ç–∫—É
    if (
      window.innerWidth >= 1200 &&
      index === currentIndex &&
      !item.hasAttribute('data-open')
    ) {
      item.classList.add('faq-focused');
    } else {
      item.classList.remove('faq-focused');
    }
  
  });
}


    // –°–∫—Ä–æ–ª–∏–º–æ –≤—ñ–¥–∫—Ä–∏—Ç–∏–π –µ–ª–µ–º–µ–Ω—Ç –ø–æ —Ü–µ–Ω—Ç—Ä—É
    const activeItem = faqItems[currentIndex];
    if (activeItem) {
      const containerWidth = faqTrack.clientWidth;
      const itemLeft = activeItem.offsetLeft;
      const itemWidth = activeItem.offsetWidth;
    if (window.innerWidth < 1200 ){
      //–∑–∞–¥–µ—Ä–∂–∫–∞, —â–æ–± –Ω–µ —Å—Ç—Ä–∏–±–∞–≤ —î–∫—Ä–∞–Ω
      setTimeout(()=>{
        activeItem.scrollIntoView({
           behavior: 'smooth',
           inline: 'center',
           block: 'nearest',
        });
      }, 1000);
      
    }
      else {
        faqTrack.scrollTo({
        left: itemLeft - (containerWidth / 2) + (itemWidth / 2),
        behavior: 'smooth',
      })
      }
      ;
    }
  }

  // –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è - –∑–Ω–∞–π—Ç–∏ –µ–ª–µ–º–µ–Ω—Ç –∑ data-open="true"
  const initiallyOpen = [...faqItems].findIndex(i => i.hasAttribute('data-open'));
  if (initiallyOpen >= 0) currentIndex = initiallyOpen;
  updateFAQ();

  // –û–±—Ä–æ–±–Ω–∏–∫ –∫–Ω–æ–ø–æ–∫ toggle
  faqItems.forEach((item, index) => {
    const toggleBtn = item.querySelector('[data-menu-handler]');
    if (toggleBtn) {
      toggleBtn.addEventListener('click', (e) => {
        e.stopPropagation(); // –©–æ–± –Ω–µ —Å–ø—Ä–∞—Ü—å–æ–≤—É–≤–∞–≤ –∫–ª—ñ–∫ –ø–æ item
        updateFAQ(true, index);
      });
    }
  });

  // –û–±—Ä–æ–±–Ω–∏–∫ –∫–ª—ñ–∫—É –ø–æ —Å–∞–º–æ–º—É item (–≤—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ —Ü–µ–π)
faqItems.forEach((item, index) => {
  item.addEventListener('click', () => {
    //e.preventDefault();
    const isOpen = item.hasAttribute('data-open');

    // –Ø–∫—â–æ —Ü—è –∂ –∫–∞—Ä—Ç–∫–∞ –≤–∂–µ –≤—ñ–¥–∫—Ä–∏—Ç–∞ ‚Äî –∑–∞–∫—Ä–∏–≤–∞—î–º–æ —ó—ó
    if (currentIndex === index && isOpen) {
      item.removeAttribute('data-open');
    } else {
      currentIndex = index;
      updateFAQ(false);
    }
  });
});

  // –ö–Ω–æ–ø–∫–∏ –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó
  prevBtns.forEach(btn => {
    btn.addEventListener('click', e => {
      e.preventDefault();
      currentIndex = (currentIndex - 1 + faqItems.length) % faqItems.length;
      updateFAQ(false);
    });
  });

  nextBtns.forEach(btn => {
    btn.addEventListener('click', e => {
      e.preventDefault();
      currentIndex = (currentIndex + 1) % faqItems.length;
      updateFAQ(false);
    });
  });
});


