document.addEventListener('DOMContentLoaded', () => {
  const banner = document.querySelector('[data-cookie-window]');
  const buttons = document.querySelectorAll('[data-cookie-action]');

  // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –Ω–∞ –∑–≥–æ–¥—É
  const consent = localStorage.getItem('cookiesConsent');

  if (!consent) {
    banner.setAttribute('data-visible', 'true');
    banner.style.display = 'block';

    // –ë–ª–æ–∫—É—î–º–æ —Å–∫—Ä–æ–ª
    document.body.style.overflow = 'hidden';
  }

  buttons.forEach(button => {
    button.addEventListener('click', () => {
      const action = button.dataset.cookieAction;

      // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –≤–∏–±—ñ—Ä
      if (action === 'accept') {
        localStorage.setItem('cookiesConsent', 'accepted');
      } else if (action === 'decline') {
        localStorage.setItem('cookiesConsent', 'declined');
      }

      // –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –±–∞–Ω–µ—Ä —ñ –¥–æ–∑–≤–æ–ª—è—î–º–æ —Å–∫—Ä–æ–ª
      banner.style.display = 'none';
      document.body.style.overflow = ''; // –ø–æ–≤–µ—Ä—Ç–∞—î–º–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–∏–π —Å–∫—Ä–æ–ª
    });
  });
});



